"use client";

import Seo from "@/components/Seo";
import Link from "next/link";

const images = [
  "/lovable-uploads/5f84fd61-a240-4aab-9d39-9728f6a27f36.png",
  "/lovable-uploads/641f2848-38da-4ddc-aeae-4d93503830ca.png",
  "/lovable-uploads/4b59fcdd-ded2-42f1-bb1c-9eb01268a427.png",
  "/lovable-uploads/dca011a1-b730-4b73-b631-80847936dfcd.png",
  "/lovable-uploads/7f3fe64f-1dd1-4d94-b585-bf9cb4ae1bd6.png",
  "/lovable-uploads/fe0b1d69-f6cd-49d5-9c99-1ed1e70daef5.png",
  "/lovable-uploads/96a63d79-9269-47b1-96ef-9727feb60536.png",
  "/lovable-uploads/304e6ac7-d932-4522-a07f-a481b9dac9c2.png",
  "/lovable-uploads/8ea10d1b-63ce-44fe-a29f-12f029415560.png",
  "/lovable-uploads/81e50e54-927c-4bb5-b22e-9cf6336b8154.png",
  "/lovable-uploads/be14d195-34f5-4c25-9618-a506bed8ecc1.png",
];

const GalleryPage = () => {
  const breadcrumbData = {
    "@context": "https://schema.org",
    "@type": "BreadcrumbList",
    itemListElement: [
      {
        "@type": "ListItem",
        position: 1,
        name: "Home",
        item: "https://myfence.com/",
      },
      {
        "@type": "ListItem",
        position: 2,
        name: "Gallery",
        item: "https://myfence.com/gallery",
      },
    ],
  };

  return (
    <main>
      <Seo
        title="Seattle Fence Gallery | Before & After Photos | MyFence"
        description="View stunning fence installations across Seattle neighborhoods. Cedar, horizontal, hogwire, and custom designs. 30+ years craftsmanship. Call (253) 455-1885."
        canonical="https://myfence.com/gallery"
        structuredData={[
          breadcrumbData,
          {
            "@context": "https://schema.org",
            "@type": "ImageGallery",
            name: "Seattle Fence Installation Gallery",
            description: "Professional fence installations in Seattle, Washington",
            creator: {
              "@type": "LocalBusiness",
              name: "MyFence.com",
              address: {
                "@type": "PostalAddress",
                addressLocality: "Seattle",
                addressRegion: "WA",
              },
              telephone: "+1-253-455-1885",
            },
          },
        ]}
      />
      <section className="container py-10">
        <h1 className="text-4xl font-bold tracking-tight mb-3">Project Gallery</h1>
        <p className="text-muted-foreground max-w-2xl">
          A look at our craftsmanship across Seattle neighborhoods. Every project is precisely planned using
          {" "}
          <Link href="/fence-genius" className="text-primary hover:underline">
            Fence Genius technology
          </Link>
          {" "}
          and executed to perfection.
        </p>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 mt-8">
          {images.map((src, i) => (
            <div key={i} className="aspect-square w-full overflow-hidden rounded-lg shadow-elevated">
              <img
                src={src}
                alt={`Seattle fence project ${i + 1} by MyFence.com`}
                loading="lazy"
                className="w-full h-full object-cover"
              />
            </div>
          ))}
        </div>
      </section>
    </main>
  );
};

export default GalleryPage;
